<template>
  <div class="bookings-container">
    <!-- Header with Buttons -->
    <div class="header">
      <div
        class="buttons"
        style="display: flex; flex-direction: row; gap: 10px"
      >
        <button class="btn-primary" @click="showCheckInForm = true">
          Check-In
        </button>
        <button class="btn-success" @click="showCheckOutForm = true">
          Check-Out
        </button>
      </div>
      <span class="booking-count">No. Bookings: {{ bookings.length }}</span>
    </div>

    <!-- Search Fields -->
    <div class="search-fields">
      <input
        v-model="search.name"
        placeholder="Search by Name"
        class="search-input"
      />
      <input
        v-model="search.nationalId"
        placeholder="Search by National ID"
        class="search-input"
      />
    </div>

    <!-- Bookings Table -->
    <table class="custom-table">
      <thead>
        <tr>
          <th>Booking ID</th>
          <th>Customer Name</th>
          <th>Check-In Time</th>
          <th>Check-Out Time</th>
          <th>Total Cost</th>
          <th>Total Time</th>
          <th>Room</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        <tr v-if="bookings.length === 0">
          <td colspan="8" style="text-align: center; color: #888">
            No check-ins found.
          </td>
        </tr>
        <tr v-for="booking in paginatedBookings" :key="booking.id">
          <td>{{ booking.id }}</td>
          <td>{{ booking.customer_name }}</td>
          <td>
            {{ formatDateTime(booking.check_in_time) || "Not Checked In" }}
          </td>
          <td>
            {{ formatDateTime(booking.check_out_time) || "Not Checked Out" }}
          </td>
          <td>
            {{ booking.total_cost ? `${booking.total_cost} EGP` : "N/A" }}
          </td>
          <td>
            {{ booking.total_time ? `${booking.total_time} minutes` : "N/A" }}
          </td>
          <td>{{ booking.room || "N/A" }}</td>
          <td>
            <button
              class="btn-delete"
              @click="confirmDeleteBooking(booking)"
              v-if="!booking.check_out_time"
            >
              Cancel Booking
            </button>
          </td>
        </tr>
      </tbody>
    </table>

    <!-- Pagination Controls -->
    <div class="pagination">
      <button @click="previousPage" :disabled="currentPage === 1">
        Previous
      </button>
      <span>Page {{ currentPage }} of {{ totalPages }}</span>
      <button @click="nextPage" :disabled="currentPage === totalPages">
        Next
      </button>
    </div>

    <!-- Check-In Form Dialog -->
    <div v-if="showCheckInForm" class="dialog-overlay">
      <div class="dialog">
        <h2>
          <Icon icon="mdi:login" width="30px" height="30px" /> Check-In Customer
        </h2>
        <form @submit.prevent="handleCheckIn" class="bookings-form">
          <div class="form-group">
            <label for="customerId"
              ><Icon icon="teenyicons:id-solid" width="24px" height="24px" />
              Customer ID:</label
            >
            <input
              v-model="checkInData.customerId"
              id="customerId"
              required
              @input="validateCustomer"
            />
            <p v-if="customerError" class="error-message">
              {{ customerError }}
            </p>
          </div>
          <div class="form-group">
            <label for="room"
              ><Icon icon="cil:room" width="24px" height="24px" /> Room:</label
            >
            <select v-model="checkInData.room" id="room" required>
              <option value="Shared Area">Shared Area</option>
              <option v-for="room in rooms" :key="room.id" :value="room.name">
                {{ room.name }}
              </option>
            </select>
          </div>
          <div class="form-actions">
            <button type="button" @click="showCheckInForm = false">
              Cancel
            </button>
            <button type="submit">Check-In</button>
          </div>
        </form>
      </div>
    </div>

    <!-- Check-Out Form Dialog -->
    <div v-if="showCheckOutForm" class="dialog-overlay">
      <div class="dialog">
        <h2>
          <Icon icon="mdi:logout" width="30px" height="30px" /> Check-Out
          Customer
        </h2>
        <form @submit.prevent="handleCheckOut" class="bookings-form">
          <div class="form-group">
            <label for="checkOutCustomerId"
              ><Icon icon="teenyicons:id-solid" width="24px" height="24px" />
              Customer ID:</label
            >
            <input
              v-model="checkOutData.customerId"
              id="checkOutCustomerId"
              required
            />
          </div>
          <div class="form-group">
            <label for="kitchenItems"
              ><Icon icon="tabler:tools-kitchen-2" width="24px" height="24px" />
              Kitchen Items:</label
            >
            <select
              v-model="checkOutData.kitchenItems"
              id="kitchenItems"
              multiple
            >
              <option
                v-for="item in kitchenItems"
                :key="item.id"
                :value="item.id"
              >
                {{ item.name }} ({{ item.price }} EGP)
              </option>
            </select>
          </div>
          <div class="form-actions">
            <button type="button" @click="showCheckOutForm = false">
              Cancel
            </button>
            <button type="submit">Check-Out</button>
          </div>
        </form>
      </div>
    </div>

    <!-- Delete Confirmation Dialog -->
    <div v-if="showDeleteConfirmation" class="dialog-overlay">
      <div class="dialog">
        <h2>
          <Icon icon="uiw:delete" width="26px" height="26px" /> Confirm Delete
        </h2>
        <p style="color: black">
          Are you sure you want to cancel this booking?
        </p>
        <div class="form-actions">
          <button type="button" @click="showDeleteConfirmation = false">
            Cancel
          </button>
          <button
            type="button"
            @click="deleteBookingConfirmed"
            class="btn-delete"
          >
            Delete
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { Icon } from "@iconify/vue";

export default {
  data() {
    return {
      bookings: [], // List of bookings
      search: {
        name: "",
        nationalId: "",
      },
      showCheckInForm: false,
      showCheckOutForm: false,
      showDeleteConfirmation: false,
      bookingToDelete: null,
      rooms: [], // Fetched from backend
      kitchenItems: [], // Fetched from backend
      checkInData: {
        customerId: "",
        room: "Shared Area",
      },
      checkOutData: {
        customerId: "",
        kitchenItems: [],
      },
      customerError: "", // Error message for customer validation
      currentPage: 1,
      itemsPerPage: 10,
    };
  },
  components: {
    Icon,
  },
  computed: {
    filteredBookings() {
      return this.bookings.filter((booking) => {
        const customerName = booking.customer_name || "";
        const searchName = this.search.name || "";
        const nationalId = booking.national_id || "";
        const searchNationalId = this.search.nationalId || "";

        return (
          customerName.toLowerCase().includes(searchName.toLowerCase()) &&
          nationalId.includes(searchNationalId)
        );
      });
    },
    paginatedBookings() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = start + this.itemsPerPage;
      return this.filteredBookings.slice(start, end);
    },
    totalPages() {
      return Math.ceil(this.filteredBookings.length / this.itemsPerPage);
    },
  },
  async created() {
    await this.loadRooms();
    await this.loadKitchenItems();
    await this.loadBookings();
  },
  methods: {
    async loadRooms() {
      try {
        const response = await axios.get("http://localhost:3000/api/rooms");
        this.rooms = response.data;
      } catch (error) {
        console.error("Error loading rooms:", error);
      }
    },
    async loadKitchenItems() {
      try {
        const response = await axios.get(
          "http://localhost:3000/api/kitchen-items"
        );
        this.kitchenItems = response.data;
      } catch (error) {
        console.error("Error loading kitchen items:", error);
      }
    },
    async loadBookings() {
      try {
        const response = await axios.get("http://localhost:3000/api/bookings");
        this.bookings = response.data;
      } catch (error) {
        console.error("Error loading bookings:", error);
      }
    },
    async validateCustomer() {
      if (!this.checkInData.customerId) {
        this.customerError = "";
        return;
      }

      try {
        const response = await axios.get(
          `http://localhost:3000/api/customers/${this.checkInData.customerId}`
        );
        if (response.data) {
          this.customerError = ""; // Customer exists
          this.checkInData.customerName = response.data.name; // Store customer name
        }
      } catch (error) {
        if (error.response && error.response.status === 404) {
          this.customerError = "Customer does not exist";
        } else {
          console.error("Error validating customer:", error);
        }
      }
    },

    async handleCheckIn() {
      if (this.customerError) {
        alert("Please fix the errors before submitting.");
        return;
      }

      try {
        const payload = {
          customer_id: this.checkInData.customerId,
          room: this.checkInData.room,
        };

        const response = await axios.post(
          "http://localhost:3000/api/bookings/check-in",
          payload
        );
        this.bookings.push(response.data);
        this.showCheckInForm = false;
        this.resetCheckInData();
      } catch (error) {
        console.error("Error checking in:", error);
        alert("Failed to check in. Please try again.");
      }
    },
    async handleCheckOut() {
      try {
        const booking = this.bookings.find(
          (b) => b.customer_id === this.checkOutData.customerId
        );

        if (!booking) {
          alert("Booking not found.");
          return;
        }

        const payload = {
          kitchen_items: this.checkOutData.kitchenItems,
        };

        const response = await axios.put(
          `http://localhost:3000/api/bookings/check-out/${booking.id}`,
          payload
        );

        // Update the booking locally
        booking.check_out_time = response.data.check_out_time;
        booking.total_time = response.data.total_time;
        booking.total_cost = response.data.total_cost;

        this.showCheckOutForm = false;
        this.resetCheckOutData();
      } catch (error) {
        console.error("Error checking out:", error);
        alert("Failed to check out. Please try again.");
      }
    },
    resetCheckInData() {
      this.checkInData = {
        customerId: "",
        room: "Shared Area",
      };
      this.customerError = "";
    },
    resetCheckOutData() {
      this.checkOutData = {
        customerId: "",
        kitchenItems: [],
      };
    },
    confirmDeleteBooking(booking) {
      this.bookingToDelete = booking;
      this.showDeleteConfirmation = true;
    },
    async deleteBookingConfirmed() {
      try {
        await axios.delete(
          `http://localhost:3000/api/bookings/${this.bookingToDelete.id}`
        );
        this.bookings = this.bookings.filter(
          (b) => b.id !== this.bookingToDelete.id
        );
        this.showDeleteConfirmation = false;
      } catch (error) {
        console.error("Error deleting booking:", error);
        alert("Failed to delete booking. Please try again.");
      }
    },
    previousPage() {
      if (this.currentPage > 1) this.currentPage--;
    },
    nextPage() {
      if (this.currentPage < this.totalPages) this.currentPage++;
    },
    formatDateTime(dateTime) {
      if (!dateTime) return null;
      const date = new Date(dateTime);
      return date.toLocaleString("en-US", {
        year: "numeric",
        month: "2-digit",
        day: "2-digit",
        hour: "2-digit",
        minute: "2-digit",
        hour12: true,
      });
    },
  },
};
</script>

<style scoped>
/* Reuse the same styles as customersPage.vue */
.bookings-container {
  padding: 20px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.booking-count {
  font-size: 1.2rem;
  font-weight: bold;
}

.search-fields {
  display: flex;
  gap: 10px;
  border-radius: 5px;
  margin-bottom: 20px;
}

.search-input {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  flex: 1;
  border: 2px solid #ffd700;
  outline: none;
}

.custom-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
  border-radius: 10px;
}

.custom-table th,
.custom-table td {
  border: 1px solid #cc9c0d;
  text-align: left;
}

.custom-table td {
  text-align: center;
  background-color: white;
  color: black;
  padding: 8px;
  white-space: nowrap;
}

.custom-table th {
  background-color: #ffd700;
  font-weight: bold;
  color: black;
  padding: 12px;
}

.custom-table tr:nth-child(even) {
  background-color: #f9f9f9;
}

.custom-table tr:hover {
  background-color: #f1f1f1;
}

.btn-primary,
.btn-success,
.btn-delete {
  padding: 8px 12px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}

.btn-primary {
  background-color: #409eff;
  color: white;
}

.btn-success {
  background-color: #ff4d4f;
  color: white;
}

.btn-delete {
  background-color: #ff4d4f;
  color: white;
}

.dialog-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.dialog {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  width: 400px;
}

.dialog h2 {
  margin-bottom: 20px;
  color: black;
  display: flex;
  flex-direction: row;
  gap: 8px;
  align-items: center;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 8px;
  color: black;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.form-actions {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.form-actions button {
  padding: 8px 12px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.form-actions button[type="submit"] {
  background-color: #409eff;
  color: white;
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  gap: 10px;
}

.pagination button {
  padding: 8px 12px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  background-color: #409eff;
  color: white;
  width: 10%;
}

.pagination button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.pagination span {
  font-size: 14px;
  color: black;
}
</style>
